import { Component } from 'react'

export default class MenuItem extends Component {
    render() {
        let className = this.props.className ? `menu-item ${this.props.className}` : "menu-item"
        return <div
            className={className}
            onClick={() => this.props.action?.(this.props.type)}
        >
            {this.props.children}
        </div>
    }
}